import React, { useEffect } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { useAction, useAppState } from '../store';

export default function AddAuth() {
  const history = useHistory();
  const loc = useLocation();
  const { fetchUserInfoList } = useAction();
  const state = useAppState();

  const init = async () => {
    await fetchUserInfoList();
  };

  useEffect(() => {
    console.log('AddAuth mount');
    console.log(JSON.stringify(loc, null, 2));
    init();
  }, []); // eslint-disable-line

  return (
    <div className="AddAuth">
      AddAuth Content
      {state.userInfoList.map(info => (
        <div key={info}>{info}</div>
      ))}
      <div onClick={() => {
        history.goBack();
      }}>go back</div>
    </div>
  );
}
